import React, { Component } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";
import { style } from "../context/style-context";

// hd
const cmpClsName = "codearea";

// props
const sizeValidationFn = (props, propName, componentName) => {
  const val = props[propName];
  const reg = /[0-9]+%?/g;
  if (!reg.test(val)) {
    return new Error(`${componentName}${propName}`);
  }
};

export default class CodeArea extends Component {
  // 
  static propTypes = {
    width: sizeValidationFn,
    height: sizeValidationFn
  };

  // props
  static defaultProps = {
    width: "100%", // 
    height: "150" // 
  };

  constructor(props) {
    super(props);
    this.state = {
      lineNums: ""
    };
  }

  /**
   *  
   *   \n 
   */
  onTextChange = e => {
    const event = e || window.event;
    const val = event.target.value;
    // 
    if (val.length === 0) {
      this.showLineNum(0);
    } else {
      // 1
      const reg = /\n/g;
      let lines = 1;
      while (reg.exec(val)) {
        lines = lines + 1;
      }
      this.showLineNum(lines);
    }
    // this.props.changeArea(val);
  };

  /**
   * 1245...setState
   * @param {*} num
   */
  showLineNum(num) {
    let numStr = "";
    for (var i = 1; i <= num; i++) {
      numStr = numStr + i + "\n";
    }
    this.setState({
      ...this.state,
      lineNums: numStr
    });
  }

  /**
   *  
   *  pscrollTopp
   */
  onScroll = e => {
    const event = e || window.event;
    const txtDom = event.target;
    const scrollVal = txtDom.scrollTop;
    const pDOM = txtDom.previousSibling;
    pDOM.scrollTop = scrollVal;
  };

  /**
   * 
   */
  setClassName() {
    const { className } = this.props;
    let cls = classNames(`${style.prefixCls}-${cmpClsName}`, className);
    return cls;
  }

  /**
   * 
   */
  getSize() {
    return {
      width: this.unitConvertor(this.props.width),
      height: this.unitConvertor(this.props.height)
    };
  }

  /**
   * px
   * @param {*} val
   */
  unitConvertor(val) {
    if (String(val).includes("%")) {
      return val;
    } else {
      return val + "px";
    }
  }

  render() {
    const size = this.getSize();
    const cls = this.setClassName();

    return (
      <div className={cls} style={size}>
        <p className={`${cls}-p`}>{this.state.lineNums}</p>

        <textarea className={`${cls}-textarea`} onChange={this.onTextChange} onScroll={this.onScroll}></textarea>
      </div>
    );
  }
}
