import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { style } from "../context/style-context";

// hd
const cmpClsName = 'vgroup';
// height
const defaultH = 'auto';
// props
const sizeValidationFn = (props, propName, componentName) => {
  const val = props[propName];
  if (val === defaultH) {
    return;
  }
  const reg = /[0-9]+%?/g;
  if (!reg.test(val)) {
    return new Error(`${componentName}${propName}`);
  }
}
// flex
const flexMapping = {
  left: 'flex-start',
  top: 'flex-start',
  right: 'flex-end',
  bottom: 'flex-end',
  center: 'center',
  justify: 'space-around',
  between: 'space-between',
};

export default class VGroup extends Component {

  // 
  static propTypes = {
    ['horizontal-align']: PropTypes.oneOf(['left', 'center', 'right']),
    ['vertical-align']: PropTypes.oneOf(['top', 'center', 'bottom', 'justify', 'between']),
    width: sizeValidationFn,
    height: sizeValidationFn,
  };

  // props
  static defaultProps = {
    ['vertical-align']: 'justify',          // 
    ['horizontal-align']: 'center',         // 
    width: '100%',                          // 
    height: 'auto',                         // 
  };

  constructor(props) {
    super(props);
  }

  /**
   * css
   */
  componentDidMount() {
    this.setCssVar();
  }

  /**
   * propscss
   */
  UNSAFE_componentWillReceiveProps() {
    this.setState({}, () => {
      this.setCssVar();
    });
  }

  /**
   * jscsscss
   */
  setCssVar() {
    const dom = this.instance;
    const attrObj = this.extraAttr;
    if (dom && attrObj) {
      for (let prop in attrObj) {
        dom.style.setProperty(prop, attrObj[prop]);
      }
    }
  }

  /**
   * 
   */
  setClassName() {
    const { className } = this.props;
    let cls = classNames(`${style.prefixCls}-${cmpClsName}`, className);
    return cls;
  }

  /**
   * propsreactextraAttrdom
   * @param {*} props 
   */
  setAttributes(props) {
    let vAlign = flexMapping[props['vertical-align']];
    let hAlign = flexMapping[props['horizontal-align']];
    let w = this.unitConvertor(props['width']);
    let h = this.unitConvertor(props['height']);

    this.extraAttr = {
      ['--width']: w,
      ['--height']: h,
      ['--vertical-align']: vAlign,
      ['--horizontal-align']: hAlign,
    };
  }

  /**
   * px
   * @param {*} val 
   */
  unitConvertor(val) {
    if (String(val).includes('%')) {
      return val;
    } else {
      return val + 'px';
    }
  }

  render() {
    this.setAttributes(this.props);
    const cls = this.setClassName();
    return (
      <div className={cls}
        ref={e => { this.instance = e }}
        {...this.props}
      >
        {this.props.children}
      </div>
    )
  }
}